<template>
  <PopUp :flow='cwwhStatus'>
    <div class="box">
      <div class="container">
        <i class="iconfont close" @click="closeCwwh">&#xe648;</i>
        <div class="title clearfix">
          <i class="iconfont title-img">&#xe62d;</i>
          <span class="title-span">{{$t("bed.cwwh")}}</span>
        </div>
        <div class="cwwh-content">
          <div class="xz-ks">
            <span>选择科室:</span>
            <el-select class='select' v-model='whDepartmentId' filterable :placeholder="$t('message.select')" @change='selectDt'>
             <el-option
               v-for="item in departmentList"
               :key="item.departmentId"
               :label="item.name"
               :value="item.departmentId">
             </el-option>
           </el-select>
          </div>
          <div class="form">
            <h5 class="form-title">{{currentDepartment.name}}</h5>
            <div class="fo-main">
              <div class="fo-item" v-for='(item,index) in currentDepartment.rooms' :key='index' v-if='item.status==0'>
                <div class="f-item">
                  <span class='fjh'>房间号</span>
                  <el-input class='fjh-value' v-model="item.name" maxlength='6' placeholder="请输入内容"></el-input>
                  <span class='cw'>床位</span>
                  <div class="cw-value">
                    <div class="cw-value-box">
                      <div class='cw-item' v-for='(it,index) in item.beds' :key='index' v-if='it.status==0'>
                        <el-input v-model="it.name" maxlength='6' @change='searchSameCw' placeholder="请输入内容" ></el-input>
                        <i class="iconfont" @click='deleteCw(it,item)'>&#xe66e;</i>
                      </div>
                      <i class="iconfont add" @click='addCw(item)'>&#xe66d;</i>
                    </div>

                  </div>
                </div>
                <button type="button" name="button" @click='deleteFj(item)'>删除</button>
              </div>
              <button type="button" name="button" class='addRoom' @click='addRoom'>新增房间</button>
            </div>
          </div>
        </div>
        <div class="ipt-group clearfix">
          <a href="javascript:void(0)" class="on" @click='save'>{{$t("button.bc")}}</a>
          <a href="javascript:void(0)" class="off" @click="closeCwwh">{{$t("button.close")}}</a>
        </div>
      </div>
    </div>
  </PopUp>
</template>

<script>
import popUp from '../../components/popUp'
import {updateHospital} from '@/api/bed'
import { mapActions,mapGetters} from 'vuex'
import {getDepartmentListD} from '@/api/monitor'
var that= {};
  export default {
    name: 'kswh',
      data(){
          return {
            // departmentList:that.$store.state.monitor.departmentList,
            whDepartmentId:'',
            whDepartmentName:'',
            rooms:[],
            currentDepartment:{},
            mbbed:{
              name:'',
              roomId:'',
              status:0,
              isEdit:0,
            }
          }
      },
      props: {
        cwwhStatus:{
          type:Boolean
        },
        departmentList:{
          type:Array
        }
      },
      beforeCreate(){
          that = this;
      },
      mounted:function(){
        that.whDepartmentId = that.departmentList[0].departmentId;
        that.departments = JSON.parse(JSON.stringify(that.departmentList))
        that.currentDepartment = that.departments[0];
        that.currentDepartment.rooms.map(function(item){
          item.beds.map(function(it,index){
            it.isEdit = 0;
          })
        })

      },
      methods:{
        ...mapActions("monitor",["getDepartmentListA"]),
        closeCwwh(){
          that.$emit('getcwwh',!that.cwwhStatus,'')
        },
        // searchSameFj(name){
        //   console.log(name)
        //   var i=0;
        //   var j=0;
        //   that.currentDepartment.rooms.map(function(item,index){
        //     if(item.name == name){
        //       i = 1;
        //     }
        //   })
        //   if(i==1){
        //     that.error('存在重复房间号'+name+',请进行修改')
        //   }
        // },
        searchSameCw(name){
          // var regu = '^[0-9a-zA-Z]{1,}$';
          // var re = new RegExp(regu);
          // if(!re.test(name)){
          //   that.error('请输入正确床位格式')
          // }
        },
        addCw(obj){
          console.log(obj)
          that.mbbed={
            name:'',
            roomId:'',
            bedId:'',
            status:0,
            isEdit:0,
          }
          var i=0;
          var regu = "^[ ]+$";
          var reu = new RegExp(regu);
          obj.beds.map(function(item){
            if(item.status!=1){
              if(item.name=='' || reu.test(item.name)){
                i = 1;
              }
            }
          })
          if(i==1){
            that.error('请先输入内容')
          }else{
            obj.beds.push(that.mbbed)
            console.log(that.departments)
          }
          console.log(obj)
          console.log(that.departments)

        },
        selectDt(departmentId){
          console.log(departmentId)
          that.whDepartmentId = departmentId;
          that.departments.map(function(item){
            if(item.departmentId == departmentId){
              that.currentDepartment = item;
            }
          })
        },
        deleteCw(obj,objf){
          console.log(obj)
          console.log(objf)
          console.log(that.departments)
          that.$confirm('确认删除该床位信息？', that.$t("account.err.czts")+'：', {
            confirmButtonText: that.$t("button.confirm"),
            cancelButtonText: that.$t("button.close"),
            type: 'warning',
            center: true
          }).then(() => {
            if(obj.bedId==''){
              objf.beds.pop(obj)
            }else{
              obj.status = 1;
            }
            console.log(that.departments)
          }).catch(() => {

          });
        },
        deleteFj(obj){
          that.$confirm('确认删除该房间信息？', that.$t("account.err.czts")+'：', {
            confirmButtonText: that.$t("button.confirm"),
            cancelButtonText: that.$t("button.close"),
            type: 'warning',
            center: true
          }).then(() => {
            console.log(that.departments)
            console.log(obj)
            if(obj.roomId==''){
              that.currentDepartment.rooms.pop(obj)
            }else{
              obj.status = 1;
            }
            console.log(that.departments)
          }).catch(() => {

          });
        },
        addRoom(){
          var mbroom = {
            beds:[
              {
                name:'',
                bedId:'',
                roomId:'',
                status:0,
                isEdit:0,
              }
            ],
            name:'',
            status:'',
            roomId:''
          }

          var i=0;
          var regu = "^[ ]+$";
          var reu = new RegExp(regu);
          that.currentDepartment.rooms.map(function(item){
            if(item.status!=1){
              if(item.name=='' || reu.test(item.name)){
                i = 1;
              }
            }
          })

          if(i==1){
            that.error('请先输入内容')
          }else{
            console.log(that.departments)
            console.log(that.currentDepartment)
            that.departments.map(function(item,index){
              if(that.currentDepartment.departmentId == item.departmentId){
                item.rooms.push(mbroom)
              }
            })
          }
        },
        save(){
          var regu = "^[ ]+$";
          var reu = new RegExp(regu);
          // var reg = '^[0-9a-zA-Z]{1,}$';
          // var re = new RegExp(reg);
          var r=0;//床位name是否符合条件的标志0符合1不符合
          var roomS = 0;
          var bedS = 0;
          console.log(that.departments)
          that.departments.map(function(item,index){
            item.rooms.map(function(it,index){
              if( it.name==''|| reu.test(it.name) ){
                item.rooms.pop(it)
              }else{
                it.beds.map(function(i,index){
                  if( i.name==''|| reu.test(i.name) ){
                    it.beds.pop(i)
                  }else{
                    // if(!re.test(i.name)){
                    //   r=1;
                    // }
                  }
                })
              }
            })
          })
          // if(r==1){
          //   that.error('请输入正确床位格式')
          // }else{
          /*判断房间号是否重复*/
          var a=0;
          var b=0;
          var nameList = [];
          var name = '';
          var name1 = '';
          var arr2 = [];//所有重复房间名字（可能多次重复）
            for(var i = 0; i < that.currentDepartment.rooms.length; i++){
      	  		for(var j = i+1 ;j < that.currentDepartment.rooms.length; j++){
      	  			if( (that.currentDepartment.rooms[i].name == that.currentDepartment.rooms[j].name)&& that.currentDepartment.rooms[j].status===0){
                  a=1;
                  arr2.push(that.currentDepartment.rooms[i].name)
                }
      	  		}
      	  	}
            console.log(name)
          if(a==1){
            var new1 = [];//去重后的数组
            arr2.map(function(item,index){
              if(new1.indexOf(item)===-1){
                new1.push(item)
                name = name+item+','
              }
            })
            that.error('存在重复房间号'+name+'请进行修改')
          }else{
            var arr = [];
            console.log(that.currentDepartment)
            that.currentDepartment.rooms.map(function(item,index){
              if(item.status===0){
                item.beds.map(function(it,index){
                  if(it.status===0){
                    arr.push(it.name)
                  }
                })
              }
            })
            console.log(arr)
            var arr1 = [];//所有重复床位名字（可能多次重复）
            for(var x = 0; x < arr.length; x++){
      	  		for(var y = x+1 ;y < arr.length; y++){
                  if( arr[x] === arr[y] ){
                    b=1;
                    arr1.push(arr[y])
                  }
      	  		}
      	  	}
            console.log(name1)
            if(b==1){
              var new2 = [];//去重后的数组
              arr1.map(function(item,index){
                if(new2.indexOf(item)===-1){
                  new2.push(item);
                  name1 = name1+item+','
                }
              })
              that.error('存在重复床位号'+name1+'请进行修改')
            }else{
              console.log(that.departments)
              var data = JSON.stringify(that.departments);
              console.log(data)
              updateHospital(data).then((res)=>{
                if(res.code==200 && res.success){
                  that.success('操作成功！');
                  getDepartmentListD().then((res)=>{
                    console.log(res)
                    if(res.code==200 && res.success){
                      that.getDepartmentListA(res.data);
                      that.$emit('getcwwh',!that.cwwhStatus,res.data[0].departmentId)
                    }else{

                    }
                  })
                }else{
                  console.log('345')
                  that.$emit('getcwwh',!that.cwwhStatus,'')
                }
              })
            }
          }
          // }
        },
      },
      components:{
          'PopUp': popUp,
      }
  }
</script>
 <style scoped lang="scss">
.cwwh-content{
  padding:35px 74px 29px;
  min-width:672px;
  .xz-ks{
    display:flex;
    justify-content: flex-start;
    span{
      display:block;
      line-height:40px;
      margin-right:10px;
    }
  }
  .form{
    // width:672px;
    margin-top:24px;
    border-radius: 8px;
    border: 2px solid #1890FF;
    h5{
      width:100%;
      height:42px;
      background: #1890FF;
      font-size: 16px;
      font-family: PingFangSC-Medium, PingFang SC;
      font-weight: 500;
      color: #FFFFFF;
      line-height: 42px;
    }
    .fo-main{
      padding:19px 22px 0;
      text-align:left;
      max-height: 360px;
      overflow: hidden;
      overflow-y: auto;
      .fo-item{
        background: #FFFFFF;
        display:flex;
        .f-item{
          border: 1px solid #BFBFBF;
          border-radius: 4px;
          display:flex;
          flex-shrink: 0;
          margin-bottom:13px;
          span{
            border-right: 1px solid #BFBFBF;
            height:100%;
            display:flex;
            align-items: center;
            justify-content: center;
          }
          .fjh{
            width:81px;
          }
          .fjh-value{
            width:106px;
            border-right: 1px solid #BFBFBF;
            height:100%;
            display:flex;
            align-items: center;
            justify-content: center;
            /deep/ .el-input__inner{
              width:106px;
              border:0;

            }
          }
          .cw{
            width:81px;
          }
          .cw-value{
            display:flex;
            position:relative;
            .cw-value-box{
              display:flex;
              padding-top:10px;
              width:640px;
              flex-wrap:wrap;
              .cw-item{
                margin-left:15px;
                margin-bottom:10px;
                position:relative;
                /deep/ .el-input__inner{
                  border: 1px solid #1890FF;
                  border-radius: 4px;
                  width:105px;
                  height:29px;
                  text-align:center;
                  font-size: 12px;
                  font-family: PingFangSC-Medium, PingFang SC;
                  font-weight: 500;
                  color: #1890FF;
                  line-height: 17px;
                }
                i{
                  font-size:14px;
                  position:absolute;
                  top:-7px;
                  right:-7px;
                  color:red;
                  cursor: pointer;
                }
              }
              .add{
                color:#1890FF;
                padding:5px 10px 0 10px;
                cursor: pointer;
              }
            }

          }

        }
        button{
          padding:0 18px;
          height: 35px;
          background: #1890FF;
          border-radius: 2px;
          border:0;
          margin-top:6px;
          font-size: 14px;
          font-family: PingFangSC-Regular, PingFang SC;
          font-weight: 400;
          color: #FFFFFF;
          line-height: 35px;
          margin-left:15px;
          cursor: pointer;
          flex-shrink: 0;
        }
      }
      .addRoom{
        padding:0 26px;
        height: 35px;
        background: #1890FF;
        border-radius: 2px;
        font-size: 14px;
        font-family: PingFangSC-Regular, PingFang SC;
        font-weight: 400;
        color: #FFFFFF;
        line-height: 35px;
        border:0;
        margin:13px auto 0;
        cursor: pointer;
        margin-bottom:37px;
      }
    }
  }
}
 </style>



