<template id="home">
  <div class="bed-all">
    <img src="../assets/img/bg.png" alt="" class='bg'>
    <Topbar>
      <i class='iconfont'>&#xe62d;</i>
      <span>{{$t("bed.title")}}</span>
    </Topbar>
    <div class="bed-main">
      <div class="bed-choose">
        <div class="bed-row">
          <div class="row-left">
            <span class="choose-title">{{$t("bed.ksxz")}}:</span>
           <div class="options">
             <div class="depart-name" @click.stop='showPan()'>
               <span v-if="$store.state.login.language==='en'">{{currentDepart?currentDepart.name:'All'}}</span>
               <span v-if="$store.state.login.language!='en'">{{currentDepart?currentDepart.name:'全部'}}</span>
               <i class='iconfont' :class="showOption?'rotate1':'rotate'" style='color:rgba(0, 0, 0, 0.25)'>&#xe628;</i>
             </div>
             <div class="depart-option" v-show='showOption' ref="showPanel">
               <div class="xz-btn" @click='toAdd'>
                  <em>+</em>
                  <span>{{$t("bed.xzks")}}</span>
               </div>
               <div class="new-depart" v-show='isAdd'>
                 <el-input
                   v-model="addDepartName"
                   class="add-depart"
                   style="width: 100px"
                   :placeholder="$t('message.fill')"
                   @keyup.enter.native='addDpartmentFn'
                   ></el-input>
                 <div class="icons">
                   <i class='iconfont' @click='addDpartmentFn'>&#xe682;</i>
                   <i class='iconfont' @click='closeAdd'>&#xe686;</i>
                 </div>
               </div>
               <div class="option-all" @click='selectAll()'>
                 <span>{{$t("bed.all")}}</span>
                 <div class="icons"></div>
               </div>
               <div class="option-item" v-for="(item,index) in optionDepartList" :key="item.departmentId">
                 <el-input
                   v-if='editRow.departmentId==item.departmentId'
                   v-model="item.name"
                   class="add-depart"
                   style="width: 100px"
                   :placeholder="$t('message.fill')"
                     @keyup.enter.native='editDepart(item)'

                 ></el-input>
                 <span v-if='editRow.departmentId!=item.departmentId' @click='selectDepartment(item)'>{{item.name}}</span>
                 <div class="icons">
                   <i class='iconfont' @click='getEditDepartRow(item)' v-if='editRow.departmentId!=item.departmentId'>&#xe687;</i>
                   <i class='iconfont' @click='editDepart(item)' v-if='editRow.departmentId==item.departmentId'>&#xe682;</i>
                   <i class='iconfont' @click='alertDelete(item)'>&#xe686;</i>
                 </div>
               </div>
             </div>
           </div>
          </div>
          <div class="row-right">
            <!-- <div class="buttons">
              <a href="javascript:void(0)" @click='ksClick'>{{$t("bed.kswh")}}</a>
              <a href="javascript:void(0)" @click='cwClick'>{{$t("bed.cwwh")}}</a>
            </div> -->
          </div>
        </div>
      </div>
      <div class="main clearfix">
        <div class="main-item" v-for="(item,index) in roomsList" :key='index' v-if='item.status!=1'>
          <span class='item-title'>{{item.name}}</span>
          <div class="items">
            <div class="item-first" @click='toAddRoom(item.departmentId)'>
              <i class='iconfont'>&#xe685;</i>
            </div>
            <div class="item" v-for="(room,index) in item.rooms" :key='index' v-if='room.status!=1'>
              <div class="item-top">
                <span>{{room.name}}</span>
                <div class="item-icon">
                  <i class='iconfont' @click='toDeleteRoom(room)'>&#xe686;</i>
                  <i class='iconfont' @click='toEditRoom(item.departmentId,room)'>&#xe687;</i>
                </div>
              </div>
              <div class="item-bottom">
                <div class="bottom-one" v-for="(bed,index) in room.beds" :key='index'>
                  <span>{{bed.name}}</span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!-- <kswh :kswhStatus='kswhStatus' v-if='kswhStatus' @getkswhStatus='getkswhStatus' :departmentList='departmentList' @getcurrentDepartmentId='getcurrentDepartmentId'></kswh> -->
    <!-- <cwwh :cwwhStatus='cwwhStatus' v-if='cwwhStatus' @getcwwhStatus='getcwwhStatus' :departmentList='departmentList' @getcurrentDepartmentId='getcurrentDepartmentId'></cwwh> -->
    <!-- <kswh :kswhStatus='kswhStatus' v-if='kswhStatus' @getkswh='getkswh' :departmentList='departmentList'></kswh>
    <cwwh :cwwhStatus='cwwhStatus' v-if='cwwhStatus' @getcwwh='getcwwh' :departmentList='departmentList'></cwwh> -->
    <scks :scksStatus='scksStatus' v-if='scksStatus' @getscksStatus='getscksStatus' :dltDepartmentObj='dltDepartmentObj'></scks>
    <addRoom :xzfjStatus='xzfjStatus' v-if='xzfjStatus' @getxzfjStatus='getxzfjStatus' :addDepartmentId='addDepartmentId'></addRoom>
    <editRoom :bjfjStatus='bjfjStatus' v-if='bjfjStatus' @getbjfjStatus='getbjfjStatus' :editRoomObj='editRoomObj' :editDepartmentId='editDepartmentId'></editRoom>
    <scfj :scfjStatus='scfjStatus' v-if='scfjStatus' @getscfjStatus='getscfjStatus' :dltRoomObj='dltRoomObj'></scfj>

  </div>
</template>

<script type="text/javascript">
import Vue from "vue";
var that= {};
import Topbar from '../components/topbar'
import kswh from '../components/bed/kswh'
import cwwh from '../components/bed/cwwh'
import scks from '../components/bed/scks'
import addRoom from '../components/bed/addroom'
import editRoom from '../components/bed/editroom'
import scfj from '../components/bed/scfj'
import { getRoomsList,editDepartment,addDepartment,deleteDepart } from '@/api/bed'
import {getDepartmentListD} from '@/api/monitor'
export default {
  name:'bed',
  data(){
   return{
     departmentList:[],//原科室列表
     roomsList:[],
     cwwhStatus:false,
     kswhStatus:false,
     showOption:false,
     xzfjStatus:false,
     bjfjStatus:false,
     scfjStatus:false,
     sccwStatus:false,
     addDepartName:'',
     dir:0,
     editRow:{},
     optionDepartList:[],//操作的科室列表
     isAdd:false,
     scksStatus:false,
     dltDepartmentObj:{},//当前要删除的科室
     currentDepart:{},//当前选择的科室
     addDepartmentId:'',//要新增房间的科室id
     editRoomObj:null,
     dltRoomObj:null,
     editDepartmentId:'',
     userInfo:that.$store.state.login.userInfo
   }
  },
  computed:{

  },
  beforeCreate(){
    that = this;
  },
  created(){
     document.addEventListener('click',(e)=>{
         if(this.$refs.showPanel){
             let isSelf = this.$refs.showPanel.contains(e.target)
             if(!isSelf){
                 that.editRow = {};
                 that.addDepartName = '';
                 that.optionDepartList = that.departmentList;
                 that.closeOption()
             }
         }
     })
  },
  mounted:function(){
    that.currentDepart = null;
    that.getRoomsListFn();

  },
  methods:{
    enterAdd(){
      console.log(event.keyCode)
      if(event.keyCode==13){
        that.addDpartmentFn();
      }

    },
    /*关闭面板*/
    closeOption(){
      that.showOption = false;
      that.isAdd = false;
    },
    showPan(){
      that.showOption = !that.showOption;
      // that.dir =
    },
    /*获取左上角科室*/
    getDepartmentList(){
      getRoomsList().then((res)=>{
        if(res.code==200 && res.success){
            that.departmentList = res.data;//存储科室列表
            that.optionDepartList = JSON.parse(JSON.stringify(res.data));//左上角科室列表
            that.optionDepartList.map(function(item){//初始化左上角科室的状态
              item.editStatus = false;
            })
          }
      })
    },
    /*获取房间*/
    getRoomsListFn(){
      if(that.currentDepart == null){
        var data={}
      }else{
        var data={
          departmentId:that.currentDepart.departmentId
        }
      }
      getRoomsList(data).then((res)=>{
        if(res.code==200 && res.success){
          if(that.currentDepart == null){
            that.departmentList = res.data;//存储科室列表
            that.optionDepartList = JSON.parse(JSON.stringify(res.data));//左上角科室列表
            that.optionDepartList.map(function(item){//初始化左上角科室的状态
              item.editStatus = false;
            })
          }
          that.roomsList = JSON.parse(JSON.stringify(res.data));//下面科室和房间
        }
      })
    },
    selectAll(){
      that.currentDepart = null;
      that.getRoomsListFn();
      that.closeOption();
    },
    /*选择某科室*/
    selectDepartment(item){
      console.log(item)
      that.currentDepart = item;
      that.getRoomsListFn();
      that.closeOption();
    },
    /*去新增房间*/
    toAddRoom(departmentId){
      that.addDepartmentId = departmentId;
      that.xzfjStatus = !that.xzfjStatus;
    },
    /*去编辑房间*/
    toEditRoom(departmentId,room){
      console.log(room)
      that.editDepartmentId = departmentId;
      that.editRoomObj = JSON.parse(JSON.stringify(room));
      that.bjfjStatus = !that.bjfjStatus;
    },
    /*去删除房间*/
    toDeleteRoom(room){
      that.dltRoomObj = room;
      that.scfjStatus = !that.scfjStatus;
    },
    getbjfjStatus(bjfjStatus,type){
      that.bjfjStatus = bjfjStatus;
      if(type==1){
        // getDepartmentListD().then((res)=>{
        //   if(res.code==200 && res.success){
        //     that.departmentList = res.data;
        //   }
        // })
        that.getRoomsListFn();
      }
    },
    getscfjStatus(scfjStatus,type){
      that.scfjStatus = scfjStatus;
      if(type==1){
        // getDepartmentListD().then((res)=>{
        //   if(res.code==200 && res.success){
        //     that.departmentList = res.data;
        //   }
        // })
        that.getRoomsListFn();
      }
    },
    /*-------------新增科室----------------*/
    /*显示新增科室*/
    toAdd(){
      that.isAdd = true
    },
    /*关闭新增科室*/
    closeAdd(){
      that.isAdd = !that.isAdd;
      that.addDepartName = '';
    },
    /*添加科室*/
    addDpartmentFn(){
      if(that.addDepartName==''){
        that.error(that.$t("bed.qsrxzks"))
      }else{
        var data={
          name:that.addDepartName,
          managerId:that.userInfo.id
        }
        addDepartment(data).then((res)=>{
          if(res.code==200 && res.success){
            that.success(that.$t("bed.xzcg"));
            that.currentDepart = null;
            that.getDepartmentList();
            that.getRoomsListFn();
            that.isAdd = false;
            that.addDepartName = '';
            that.editRow = {};
          }
        })
      }
    },
    /*-------------新增床位----------------*/
    /*新增床位组件显示*/
    getxzfjStatus(xzfjStatus,type){
      that.xzfjStatus = xzfjStatus;
      if(type==1){
        that.getRoomsListFn();
      }
    },
    /*-------------编辑科室----------------*/
    /*选择编辑的科室*/
    getEditDepartRow(row){
      console.log(row)
      that.editRow = row;
    },
    /*编辑科室*/
    editDepart(item){
      console.log(that.optionDepartList)
      console.log(item)
      if(item.name===''){
        that.error(that.$t("bed.qsrbjks"))
      }else{
        var data = {
          departmentId:item.departmentId,
          name:item.name
        }
        console.log(data)
        editDepartment(data).then((res)=>{
          if(res.code==200 && res.success){
            that.success(that.$t("bed.bjcg"));
            console.log(that.editRow)
            console.log(that.currentDepart)
            if(that.currentDepart == null){

            }else{
              that.currentDepart = JSON.parse(JSON.stringify(that.editRow));
            }
            that.getDepartmentList();
            that.getRoomsListFn();
            that.editRow = {};
          }
        })
      }
    },
    /*-------------删除科室----------------*/
    /*提示删除科室*/
    alertDelete(item){
      console.log(item)
      that.scksStatus = !that.scksStatus;
      that.dltDepartmentObj = item;
      that.closeOption();
    },
    getscksStatus(scksStatus,type){
      that.scksStatus = scksStatus;
      if(type==1){
        that.getDepartmentList();
        console.log(that.dltDepartmentObj.departmentId)
        if(that.currentDepart == null){

        }else{
          if(that.currentDepart.departmentId == that.dltDepartmentObj.departmentId){
            that.currentDepart = null;
          }else{

          }
        }
        that.getRoomsListFn();
      }
    },

  },
  components:{
    Topbar,
    scks,
    addRoom,
    editRoom,
    scfj,
    kswh,
    cwwh
  }
}
</script>
<style lang="scss">
html,body{
width:100%;
height:100%;
  .bed-all{
    width:100%;
    background-size:cover;
    height:100%;
    position:relative;
    padding-top:40px;
    .bed-main{
      position:relative;
      margin:0 auto;
      width:99%;
      .bed-choose{
        width:98%;
        margin:0 auto;
        padding-top: 47px;
        display:flex;
        flex-direction: column;
        .bed-row{
          display:flex;
          flex-direction: row;
          justify-content: space-between;
          margin-bottom:23px;
          .row-left{
            width:50%;
            display:flex;
            flex-direction: row;
            .choose-title{
              font-size:18px;
              font-family:PingFangSC-Medium,PingFang SC;
              font-weight:600;
              color:rgba(0,0,0,1);
              line-height:36px;
              margin-right:7px;
              height:36px;
            }
            .choose-title1{
              margin:0 17px;
            }
            .rotate1{       //旋转后的位置
            　　transform:rotate(45deg);
            transition:all .3s linear;
            　　-webkit-transform:rotate(45deg);    //还是兼容一下
            　　-webkit-transtion:all .3s linear;
            }
            .rotate{          //初始状态的旋转位置
            　　transform:rotate(0);
            transition:all .3s linear;
            　　-webkit-transform:rotate(0);
            　　-webkit-transtion:all .3s linear;
            }
            .options{
              position:relative;
              .depart-name{
                width:160px;
                height:40px;
                padding:0 12px;
                background:#fff;
                border:1px solid #DCDFE6;
                cursor: pointer;
                display:flex;
                justify-content: space-between;
                align-items: center;
                i{
                  font-size:16px;

                }
                .rotate{
                  transform:rotate(-90deg);
                }
                .rotate1{
                  transform:rotate(90deg);
                }
              }
              .depart-option::-webkit-scrollbar{
                display:none;
              }
              .depart-option{
                position:absolute;
                top:45px;
                left:0;
                background:#fff;
                height:200px;
                overflow-y: scroll;
                width:184px;
                background: #FFFFFF;
                box-shadow: 0px 9px 28px 8px rgba(0, 0, 0, 0.05), 0px 6px 16px 0px rgba(0, 0, 0, 0.08), 0px 3px 6px -4px rgba(0, 0, 0, 0.12);
                border-radius: 2px;
                /deep/ .el-input__inner{
                  border:0;
                  padding:0;
                }
                .xz-btn{
                  background: #F6FAFF;
                  display:flex;
                  justify-content: flex-start;
                  align-items: center;
                  cursor: pointer;
                  height:40px;
                  padding:0 12px;
                  em{
                    color:#1890FF;
                    font-style: normal;
                    margin:0 8px 0 0;
                  }
                  span{
                    color:#1890FF;
                    font-size: 14px;
                    font-family: PingFangSC-Regular, PingFang SC;
                    font-weight: 400;
                    color: #1890FF;
                  }
                }
                .new-depart{
                  display:flex;
                  justify-content: space-around;
                  align-items: center;
                  .add-depart{

                  }
                  .icons{
                    i{
                      color:#1890FF;
                      cursor: pointer;
                    }
                  }
                }
                .option-item{
                  height:40px;
                  padding:0 12px;
                  display:flex;
                  justify-content: space-between;
                  align-items: center;
                  span{
                    width:100px;
                    font-size: 14px;
                    font-family: PingFangSC-Regular, PingFang SC;
                    font-weight: 400;
                    color: rgba(0, 0, 0, 0.65);
                    cursor: pointer;
                  }
                  .icons{
                    i{
                      color:#1890FF;
                      cursor: pointer;
                    }
                  }
                }
                .option-all{
                  height:40px;
                  padding:0 12px;
                  display:flex;
                  justify-content: flex-start;
                  align-items: center;
                  span{
                    width:100px;
                    font-size: 14px;
                    font-family: PingFangSC-Regular, PingFang SC;
                    font-weight: 400;
                    color: rgba(0, 0, 0, 0.65);
                    cursor: pointer;
                  }
                }
              }
            }
          }
          .row-right{
            width:50%;
            display:flex;
            flex-direction: row;
            justify-content: flex-end;
            .buttons{
              display:flex;
              flex-direction: row;
              a{
                display:block;
                width:109px;
                height:35px;
                background:rgba(24,144,255,1);
                border-radius:2px;
                font-size:14px;
                font-family:PingFangSC-Regular,PingFang SC;
                font-weight:400;
                color:rgba(255,255,255,1);
                line-height:35px;
                text-align:center;
                margin-right:12px;
              }
              a:last-of-type{
                margin-right:0;
              }
            }
          }
        }
      }
      .main{
        display:flex;
        flex-direction: row;
        flex-wrap: wrap;
        width:100%;
        .main-item{
          width:100%;
          .item-title{
            margin-left:125px;
            font-family: PingFangSC-Medium;
            font-size: 24px;
            color: #000000;
            text-align: left;
            display:block;
            float:left;
            margin:26px 0;
            padding-left:18px;
          }
          .items{
            display:flex;
            flex-direction: row;
            flex-wrap: wrap;
            // justify-content: flex-start;
            // align-items: flex-start;
            width:100%;
            .item-first{
              width: 355px;
              min-height: 166px;
              cursor: pointer;
              background: #FFFFFF;
              box-shadow: 0px 2px 11px 0px rgba(210, 210, 210, 0.29);
              border-radius: 4px;
              display:flex;
              justify-content: center;
              align-items: center;
              margin-bottom:26px;
              margin-left:18px;
              i{
                font-size:26px;
                color:rgba(186, 194, 219, 1);
              }
            }
            .item{
              width: 355px;
              min-height: 166px;
              float: left;
              border: 1px solid #ddd;
              background: #fff;
              box-sizing: border-box;
              padding:0 16px;
              margin-left:18px;
              margin-bottom:26px;
              .item-top{
                height:57px;
                display:flex;
                justify-content: space-between;
                align-items: center;
                span{
                  font-size: 18px;
                  font-family: PingFangSC-Medium, PingFang SC;
                  font-weight: 500;
                  color: #1D1D1D;
                  line-height: 25px;
                }
                .item-icon{
                  display:flex;
                  justify-content: space-between;
                  width:50px;
                  i{
                    color:#1890FF;
                    cursor: pointer;
                  }
                }
              }
              .item-bottom{
                display:flex;
                flex-wrap: wrap;
                .bottom-one{
                  padding:15px 0;
                  flex-shrink: 0;
                  border-top:1px solid #F0F0F0;
                  span{
                    display:block;
                    padding:0 8px;
                    border-right:1px solid rgba(240, 240, 240, 1);
                    font-family: PingFangSC-Medium;
                    font-size: 14px;
                    color: #000000;
                    text-align: center;
                    display:flex;
                    justify-content: center;
                    align-items: center;
                  }
                }
                .bottom-one:nth-child(5n+1){
                  span{
                      border-left:none
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}

</style>



