<template>
<div class="" v-if='xzfjStatus'>
  <PopUp :flow='xzfjStatus'>
    <div class="box">
      <div class="container">
        <i class="iconfont close" @click='closeXzfj'>&#xe648;</i>
        <div class="title clearfix">
          <i class="iconfont title-img">&#xe62f;</i>
          <span class="title-span">{{$t("bed.xzfj")}}</span>
        </div>
        <div class="xzfj-content">
          <div class="xzfj-item">
            <span>{{$t("bed.fjh")}}：</span>
            <el-input v-model="newRoom.room.name" class='select' :placeholder="$t('bed.ts')"></el-input>
          </div>
          <div class="xzfj-item">
            <span>{{$t("bed.cwh")}}：</span>
            <div class="beds">
              <div class="cw-value-box">
                <div class="cw-item" v-for='(item,index) in newRoom.room.beds' :key='index'>
                  <el-input v-model="item.name" :placeholder="$t('bed.cwh')" ></el-input>
                  <i class="iconfont" @click='deleteCw(index)'>&#xe66e;</i>
                </div>
                <i class="iconfont add" @click='addCw()'>&#xe66d;</i>
              </div>
            </div>
          </div>
          <div class="tips">
            <i class='iconfont'>&#xe667;</i>
            <span>{{$t("bed.cwbkcf")}}</span>
          </div>
        </div>
        <div class="ipt-group clearfix">
          <a href="javascript:void(0)" class="on" @click='addDepartFn'>{{$t('button.confirm')}}</a>
          <a href="javascript:void(0)" class="off" @click='closeXzfj'>{{$t('button.close')}}</a>
        </div>
      </div>
    </div>
  </PopUp>
  <sccw :sccwStatus='sccwStatus' v-if='sccwStatus' @getsccwStatus='getsccwStatus' :deleteIndex='deleteIndex' :roomObj='newRoom.room'></sccw>
</div>
</template>

<script>
import popUp from '../../components/popUp'
import sccw from '../bed/sccw'
import {addRoom } from '@/api/bed'
import {getDepartmentListD} from '@/api/monitor'
var that= {};
  export default {
    name: 'scks',
      data(){
          return {
            newRoom:{
              departmentId:'',
              room:{
                beds:[
                  {name:''}
                ],
                name:'',
              }
            },
            sccwStatus:false,
            delectBedObj:{},
            deleteIndex:''
          }
      },
      props:{
        xzfjStatus:{
          type:Boolean
        },
        addDepartmentId:{
          type:Number
        }
      },
      beforeCreate(){
          that = this;
      },
      mounted:function(){
        that.newRoom.departmentId = that.addDepartmentId;
      },
      methods:{
        closeXzfj(){
          that.$emit('getxzfjStatus',!that.xzfjStatus);
        },
        addDepartFn(){
          var reg = new RegExp("^[\u4e00-\u9fa5a-zA-Z0-9]+$");
          console.log(that.newRoom)
          if(that.newRoom.room.name==''){
            that.error(that.$t("bed.qscfjh"))
          }else if(that.newRoom.room.name.length>12){
            that.error(that.$t("bed.fjhbndy12"))
          }else if(!reg.test(that.newRoom.room.name)){
            that.error(that.$t("bed.qsryxzf"))
          }else{
            function checkAdult(item){//格式检测
              return reg.test(item.name)
            }
            function checkLength(item){//长度检测
              return item.name.length<13
            }
            if(that.newRoom.room.beds.every(checkAdult)){//床位格式通过检测
              if(that.newRoom.room.beds.every(checkLength)){//床位长度通过检测
                console.log(that.newRoom)
                var a=0;
                var arr1 = [];
                for(var i = 0; i < that.newRoom.room.beds.length; i++){
          	  		for(var j = i+1 ;j < that.newRoom.room.beds.length; j++){
          	  			if( that.newRoom.room.beds[i].name === that.newRoom.room.beds[j].name){
                      console.log(that.newRoom.room.beds[i].name)
                      a=1;
                      arr1.push(that.newRoom.room.beds[i].name)
                    }
          	  		}
          	  	}
                console.log(a)
                if(a==1){
                  var name = '';
                  var new1 = [];//去重后的数组
                  arr1.map(function(item,index){
                    if(new1.indexOf(item)===-1){
                      new1.push(item)
                      name = name+item+','
                    }
                  })
                  that.error(that.$t("bed.czcfcwh")+name+that.$t("bed.qjxxg"))
                }else{
                  console.log(that.newRoom)
                  addRoom(that.newRoom).then((res)=>{
                    if(res.success && res.code==200){
                      that.success(that.$t("bed.xzcg"));
                      that.$emit('getxzfjStatus',!that.xzfjStatus,1);
                    }
                  })
                }
              }else{
                that.error(that.$t("bed.cwhbndy12"))
              }
            }else{
              that.error(that.$t("bed.qsryxzf"))
            }
          }
        },
        deleteCw(index){
          that.sccwStatus = !that.sccwStatus;
          that.deleteIndex = index;
        },
        deleteBedFn(){
          that.newRoom.room.beds.splice(index,1);
        },
        getsccwStatus(sccwStatus,type){
          that.sccwStatus = sccwStatus;
        },
        addCw(item){
          var obj = {
            name:''
          }
          that.newRoom.room.beds.push(obj)
          console.log(that.newRoom.room.beds)
        },
      },
      components:{
          'PopUp': popUp,
          sccw
      }
  }
</script>
<style scoped lang="scss">
.xzfj-content{
  border:0;
  display:flex;
  justify-content: center;
  margin:0 42px;
  flex-direction:column;
  .xzfj-item{
    display:flex;
    margin-bottom:26px;
    span{
      width:80px;
      display:block;
      font-size: 16px;
      font-family: PingFangSC-Medium, PingFang SC;
      font-weight: 500;
      color: #333333;
      text-align:center;
      line-height:36px;
    }
    /deep/ .select{
      width:470px;
      height:36px;
      line-height:36px;
      border:0;
      border: 1px solid #BFBFBF;
      border-radius: 3px;
      /deep/ .el-input__inner{
        border:0;
        height:34px;
      }
    }
    .beds{
      width: 470px;
      min-height: 120px;
      background: #FFFFFF;
      border-radius: 3px;
      border: 1px solid #BFBFBF;
      padding:14px 0 0 0;
      .cw-value-box{
        display:flex;
        flex-direction: row;
        flex-wrap: wrap;
        padding-bottom:18px;
        .cw-item{
          width: 100px;
          height:36px;
          line-height:36px;
          margin-left:15px;
          margin-bottom:10px;
          position:relative;
          border-radius: 3px;
          i{
            font-size:14px;
            position:absolute;
            top:-16px;
            right:-7px;
            color:red;
            cursor: pointer;
          }
        }
        .add{
          color:#1890FF;
          padding:12px 10px 0 10px;
          cursor: pointer;
        }
      }
    }
  }
  .tips{
    display:flex;
    padding-left:80px;
    i{
      color:#E02020;
    }
    span{
      font-size: 14px;
      font-family: PingFangSC-Regular, PingFang SC;
      font-weight: 400;
      color: #E02020;
      line-height: 20px;
      padding-left:10px;
    }
  }
}
</style>



