<template>
  <PopUp :flow='scksStatus'>
    <div class="box">
      <div class="container">
        <i class="iconfont close" @click='closeScks'>&#xe648;</i>
        <div class="title clearfix">
          <i class="iconfont title-img">&#xe667;</i>
          <span class="title-span">{{$t("bed.scks")}}</span>
        </div>
        <div class="scks-content">
          <h5>{{$t("bed.scksts1")}}</h5>
          <div class="important-tips">
            <h6>{{$t("bed.scksts2")}}</h6>
            <p>1.{{$t("bed.scksts3")}}</p>
            <p>2.{{$t("bed.scksts4")}}</p>
            <p>3.{{$t("bed.scksts5")}}</p>
          </div>

        </div>
        <div class="ipt-group clearfix">
          <a href="javascript:void(0)" class="on" @click='deleteDepartFn'>{{$t('button.confirm')}}</a>
          <a href="javascript:void(0)" class="off" @click='closeScks'>{{$t('button.close')}}</a>
        </div>
      </div>
    </div>
  </PopUp>
</template>

<script>
import popUp from '../../components/popUp'
import {deleteDepart } from '@/api/bed'
import {getDepartmentListD} from '@/api/monitor'
var that= {};
  export default {
    name: 'scks',
      data(){
          return {

          }
      },
      props:{
        scksStatus:{
          type:Boolean
        },
        dltDepartmentObj:{
          type:Object
        }
      },
      beforeCreate(){
          that = this;
      },
      mounted:function(){

      },
      methods:{
        closeScks(){
          that.$emit('getscksStatus',!that.scksStatus);
        },
        deleteDepartFn(){
          var data={
            departmentId:that.dltDepartmentObj.departmentId
          }
          deleteDepart(data).then((res)=>{
            if(res.success && res.code==200){
              that.success(that.$t("bed.sccg"))
              that.$emit('getscksStatus',!that.scksStatus,1);

            }
          })
        },
      },
      components:{
          'PopUp': popUp,
      }
  }
</script>
<style scoped lang="scss">
.scks-content{
  border:0;
  width:805px;
  display:flex;
  margin:0 42px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  h5{
    font-size: 22px;
    font-family: PingFangSC-Medium, PingFang SC;
    font-weight: 500;
    color: #232323;
    line-height: 30px;
    letter-spacing: 1px;
    width:100%;
  }
  .important-tips{
    width: 472px;
    padding-bottom:10px;
    border: 1px solid #E02020;
    margin-top:34px;
    h6{
      font-size: 20px;
      font-family: PingFangSC-Medium, PingFang SC;
      font-weight: 500;
      color: #E02020;
      line-height: 28px;
      margin:21px 0 11px;
    }
    p{
      font-size: 18px;
      font-family: PingFangSC-Regular, PingFang SC;
      font-weight: 400;
      color: #E02020;
      line-height: 32px;
    }
  }
}
</style>



