<template>
<div class="" v-if='bjfjStatus'>
  <PopUp :flow='bjfjStatus'>
    <div class="box">
      <div class="container">
        <i class="iconfont close" @click='closeBjfj'>&#xe648;</i>
        <div class="title clearfix">
          <i class="iconfont title-img">&#xe62f;</i>
          <span class="title-span">{{$t("bed.bjfj")}}</span>
        </div>
        <div class="xzfj-content">
          <div class="xzfj-item">
            <span>{{$t("bed.fjh")}}：</span>
            <el-input v-model="editRoomObj.name" class='select' :placeholder="$t('bed.ts')"></el-input>
          </div>
          <div class="xzfj-item">
            <span>{{$t("bed.cwh")}}：</span>
            <div class="beds">
              <div class="cw-value-box">
                <div class="cw-item" v-for='(item,index) in editRoomObj.beds' :key='index'>
                  <el-input v-model="item.name" :placeholder="$t('bed.cwh')"></el-input>
                  <i class="iconfont" @click='deleteCw(index)'>&#xe66e;</i>
                </div>
                <i class="iconfont add" @click='addCw()'>&#xe66d;</i>
              </div>
            </div>
          </div>
          <div class="tips">
            <i class='iconfont'>&#xe667;</i>
            <span>{{$t("bed.cwbkcf")}}</span>
          </div>
        </div>
        <div class="ipt-group clearfix">
          <a href="javascript:void(0)" class="on" @click='editDepartFn'>{{$t('button.confirm')}}</a>
          <a href="javascript:void(0)" class="off" @click='closeBjfj'>{{$t('button.close')}}</a>
        </div>
      </div>
    </div>

      <!-- <div class="box box-sccw" v-if='sccwStatus'>
        <div class="container">
          <i class="iconfont close" @click='sccwStatus = !sccwStatus'>&#xe648;</i>
          <div class="title clearfix">
            <i class="iconfont title-img">&#xe62f;</i>
            <span class="title-span">确认删除</span>
          </div>
          <div class="scks-content">
            <p>删除后，该床位将不能被正常使用,确定删除吗？</p>
          </div>
          <div class="ipt-group clearfix">
            <a href="javascript:void(0)" class="on" @click='deleteBedFn'>{{$t('button.confirm')}}</a>
            <a href="javascript:void(0)" class="off" @click='sccwStatus = !sccwStatus'>{{$t('button.close')}}</a>
          </div>
        </div>
      </div> -->
      <!-- <sccw :sccwStatus='sccwStatus' v-if='sccwStatus' @getsccwStatus='getsccwStatus' :deleteIndex='deleteIndex' :editRoomObj='editRoomObj'></sccw> -->
  </PopUp>
  <sccw :sccwStatus='sccwStatus' v-if='sccwStatus' @getsccwStatus='getsccwStatus' :deleteIndex='deleteIndex' :roomObj='editRoomObj'></sccw>
</div>
</template>

<script>
import popUp from '../../components/popUp'
import sccw from '../bed/sccw'
import {editRoom } from '@/api/bed'
import {getDepartmentListD} from '@/api/monitor'
var that= {};
  export default {
    name: 'scks',
      data(){
          return {
            sccwStatus:false,
            delectBedObj:{},
            deleteIndex:''
          }
      },
      props:{
        bjfjStatus:{
          type:Boolean
        },
        editRoomObj:{
          type:Object
        },
        editDepartmentId:{
          type:Number
        }
      },
      beforeCreate(){
          that = this;
      },
      mounted:function(){
        console.log(that.editRoomObj)
      },
      methods:{
        closeBjfj(){
          that.$emit('getbjfjStatus',!that.bjfjStatus);
        },
        editDepartFn(){
          var reg = new RegExp("^[\u4e00-\u9fa5a-zA-Z0-9]+$");
          if(that.editRoomObj.name==''){
            that.error(that.$t("bed.qscfjh"))
          }else if(that.editRoomObj.name.length>12){
            that.error(that.$t("bed.fjhbndy12"))
          }else if(!reg.test(that.editRoomObj.name)){
            that.error(that.$t("bed.qsryxzf"))
          }else{
            function checkAdult(item){//格式检测
              return reg.test(item.name)
            }
            function checkLength(item){//长度检测
              return item.name.length<13
            }
            function checkLength(item){//是否有重复
              return item.name.length<13
            }
            if(that.editRoomObj.beds.every(checkAdult)){//床位格式通过检测
              if(that.editRoomObj.beds.every(checkLength)){//床位长度通过检测
                var a=0;
                var arr1 = [];
                for(var i = 0; i < that.editRoomObj.beds.length; i++){
          	  		for(var j = i+1 ;j < that.editRoomObj.beds.length; j++){
          	  			if( that.editRoomObj.beds[i].name === that.editRoomObj.beds[j].name){
                      a=1;
                      arr1.push(that.editRoomObj.beds[i].name)
                    }
          	  		}
          	  	}
                if(a==1){
                  var name = '';
                  var new1 = [];//去重后的数组
                  arr1.map(function(item,index){
                    if(new1.indexOf(item)===-1){
                      new1.push(item)
                      name = name+item+','
                    }
                  })
                  that.error(that.$t("bed.czcfcwh")+name+that.$t("bed.qjxxg"))
                }else{
                  console.log(that.editRoomObj)
                  var obj = {};
                  obj.name = that.editRoomObj.name;
                  obj.beds = that.editRoomObj.beds;
                  obj.roomId = that.editRoomObj.roomId;
                  obj.departmentId = that.editDepartmentId;
                  console.log(obj)
                  editRoom(obj).then((res)=>{
                    if(res.success && res.code==200){
                      that.success(that.$t("bed.bjcg"));
                      that.$emit('getbjfjStatus',!that.bjfjStatus,1);
                    }
                  })
                }
              }else{
                that.error(that.$t("bed.cwhbndy12"))
              }
            }else{
              that.error(that.$t("bed.qsryxzf"))
            }
          }
        },
        deleteCw(index){
          that.sccwStatus = !that.sccwStatus;
          that.deleteIndex = index;
          console.log(that.deleteIndex)
          // that.editRoomObj.beds.splice(index,1);
        },
        deleteBedFn(){
          that.editRoomObj.beds.splice(that.deleteIndex,1);
        },
        getsccwStatus(sccwStatus,type){
          that.sccwStatus = sccwStatus;
        },
        addCw(item){
          var obj = {
            name:''
          }
          that.editRoomObj.beds.push(obj)
          console.log(that.editRoomObj.beds)
        },
      },
      components:{
          'PopUp': popUp,
          sccw
      }
  }
</script>
<style scoped lang="scss">
.xzfj-content{
  border:0;
  display:flex;
  justify-content: center;
  margin:0 42px;
  flex-direction:column;
  .xzfj-item{
    display:flex;
    margin-bottom:26px;
    span{
      width:80px;
      display:block;
      font-size: 16px;
      font-family: PingFangSC-Medium, PingFang SC;
      font-weight: 500;
      color: #333333;
      text-align:center;
      line-height:36px;
    }
    /deep/ .select{
      width:470px;
      height:36px;
      line-height:36px;
      border:0;
      border: 1px solid #BFBFBF;
      border-radius: 3px;
      /deep/ .el-input__inner{
        border:0;
        height:34px;
      }
    }
    .beds{
      width: 470px;
      height: 160px;
      background: #FFFFFF;
      border-radius: 3px;
      border: 1px solid #BFBFBF;
      overflow-y: scroll;
      .cw-value-box{
        display:flex;
        min-height: 125px;
        padding:14px 0 0 0;
        flex-direction: row;
        flex-wrap: wrap;
        padding-bottom:18px;

        .cw-item{
          width: 100px;
          height:36px;
          line-height:36px;
          margin-left:15px;
          margin-bottom:10px;
          position:relative;
          border-radius: 3px;
          i{
            font-size:14px;
            position:absolute;
            top:-16px;
            right:-7px;
            color:red;
            cursor: pointer;
          }
        }
        .add{
          color:#1890FF;
          padding:12px 10px 0 10px;
          cursor: pointer;
        }
      }

    }
    .beds::-webkit-scrollbar {
      display: none;
    }
  }
  .xzfj-item:nth-child(2){
    margin-bottom:9px;
  }
  .tips{
    display:flex;
    padding-left:80px;
    i{
      color:#E02020;
    }
    span{
      font-size: 14px;
      font-family: PingFangSC-Regular, PingFang SC;
      font-weight: 400;
      color: #E02020;
      line-height: 20px;
      padding-left:10px;
    }
  }
}
</style>



