<template>
  <PopUp :flow='kswhStatus'>
    <div class="box">
      <div class="container">
        <i class="iconfont close" @click="closeKswh">&#xe648;</i>
        <div class="title clearfix">
          <i class="iconfont title-img">&#xe62d;</i>
          <span class="title-span">{{$t("bed.kswh")}}</span>
        </div>
        <div class="kswh-content content">
          <div class="form">
            <span class='ks-left'>科室:</span>
            <div class="kslist">
              <div :class="item.isEdit==0?'ks-blue':'ks-gray'" class='ks-item' v-for='(item,index) in rooms' :key='index' v-if='item.status!=1'>
                <span v-if='item.isEdit==0 && item.status==0' @dblclick='dbC(item)'>{{item.name}}</span>
                <el-input v-model="item.name" placeholder="请输入内容" v-if='item.isEdit==1'>{{item.isEdit}}</el-input>
                <i class="iconfont" @click='deleteKs(item)'>&#xe66e;</i>
              </div>
              <i class="iconfont add" @click='addKs'>&#xe66d;</i>
            </div>
          </div>
        </div>
        <div class="ipt-group clearfix">
          <a href="javascript:void(0)" class="on" @click='save'>{{$t("button.bc")}}</a>
          <a href="javascript:void(0)" class="off" @click="closeKswh">{{$t("button.close")}}</a>
        </div>
      </div>
    </div>
  </PopUp>
</template>

<script>
import popUp from '../../components/popUp'
import {updateHospital} from '@/api/bed'
import { mapActions,mapGetters} from 'vuex'
import {getDepartmentListD} from '@/api/monitor'

var that= {};
  export default {
    name: 'kswh',
      data(){
          return {
            rooms:[],
            mbKs:{
              name:'',
              departmentId:'',
              isEdit:1,/*1可编辑0不可编辑*/
              status:'',
              rooms:[],
            },
          }
      },
      props: {
        currentDepartmentId:{
          type:Number
        },
        departmentList:{
          type:Array
        },
        kswhStatus:{
          type:Boolean
        },
      },
      beforeCreate(){
          that = this;
      },
      computed:{
        ...mapGetters("login",["useDepartId","role"])
      },
      mounted:function(){
        // getDepartmentListD().then((res)=>{
        //   if(res.code==200 && res.success){
        //     that.departmentList = res.data;
        //
        //   }
        // })
        that.rooms = JSON.parse(JSON.stringify(that.departmentList))
        that.rooms.map(function(item){
          item.isEdit = 0;
        })
        console.log(that.rooms)
      },
      methods:{
        ...mapActions("monitor",["getDepartmentListA"]),
        closeKswh(){
          that.$emit('getkswh',!that.kswhStatus,'')
        },
        // searchSameKs(name){
        //   console.log(name)
        //   var i=0;
        //   that.rooms.map(function(item,index){
        //     if(item.name == name){
        //       i = 1;
        //     }
        //   })
        //   if(i==1){
        //     that.error('存在重复科室号'+name+',请进行修改')
        //   }
        // },
        addKs(){
          that.mbKs={
            name:'',
            departmentId:'',
            isEdit:1,/*1可编辑0不可编辑*/
            status:'',
            rooms:[],
          }
          var i=0;
          var regu = "^[ ]+$";
          var re = new RegExp(regu);
          that.rooms.map(function(item){
            if(item.name=='' || re.test(item.name)){
              i = 1;
            }
          })
          if(i==1){
            that.error('请先输入内容')
          }else{
            that.rooms.push(that.mbKs);
          }
        },
        dbC(obj){
          console.log(obj.departmentId)
          that.rooms.map(function(item,index){
            if(item.departmentId==obj.departmentId){
              item.isEdit = 1;
              that.$set(that.rooms,index,item)
            }
          })
        },
        /*保存*/
        save(){
          that.rooms.map(function(item,index){
            // that.$delete(item,'createTime')
            // that.$delete(item,'hospitalName')
            var regu = "^[ ]+$";
            var re = new RegExp(regu);
            if(item.name==''|| re.test(item.name)){
              that.rooms.pop(item)
            }else{}

          })
          console.log(that.rooms)
          var a=0;
          var name;
          for(var i = 0; i < that.rooms.length; i++){
    	  		for(var j = i+1 ;j < that.rooms.length; j++){
    	  			if(that.rooms[i].name == that.rooms[j].name)
    	  				a=1;
                name = that.rooms[j].name;
    	  		}
    	  	}
          console.log(a)
          if(a==1){
            that.error('存在重复科室号'+name+',请进行修改')
          }else{
            console.log(that.rooms)
            var data = JSON.stringify(that.rooms);
            updateHospital(data).then((res)=>{
              if(res.code==200 && res.success){
                that.success(that.$t("message.OperSuccess"));
                getDepartmentListD().then((res)=>{
                  console.log(res)
                  that.getDepartmentListA(res.data);
                  that.$emit('getkswh',!that.kswhStatus,res.data[0].departmentId)
                })
              }
            })
          }

        },
        /*删除*/
        deleteKs(item){
          console.log(item.departmentId)
          that.$confirm('删除科室时，其关联的房间/床位信息都会被删除，是否确认操作?', that.$t("account.err.czts")+'：', {
            confirmButtonText: that.$t("button.confirm"),
            cancelButtonText: that.$t("button.close"),
            type: 'warning',
            center: true
          }).then(() => {
            that.rooms.map(function(it,index){
              if(item.departmentId==it.departmentId){
                it.status = 1;
              }
            })
            console.log(that.rooms)
            // var data = JSON.stringify(that.rooms);
            // updateHospital(data).then((res)=>{
            //   if(res.code==200 && res.success){
            //     that.success(that.$t("message.OperSuccess"));
            //     getDepartmentListD().then((res)=>{
            //       console.log(res)
            //       that.getDepartmentListA(res.data);
            //       that.$emit('getcurrentDepartmentId',res.data[0].departmentId);
            //     })
            //   }
            // })
          }).catch(() => {

          });
        },
      },
      components:{
          'PopUp': popUp,
      }
  }
</script>
 <style scoped lang="scss">
.kswh-content{
  margin:0 128px;
  padding:35px 74px 29px;
  .form{
    display:flex;
    width:672px;
    .ks-left{
      font-size: 16px;
      font-family: PingFangSC-Regular, PingFang SC;
      font-weight: 400;
      color: #666666;
      line-height: 36px;
      flex-shrink:0;
    }
    .kslist{
      display:flex;
      flex-wrap: wrap;
      .add{
        font-size:25px;
        line-height:36px;
        margin-left:20px;
        color:#1890FF;
        cursor: pointer;
      }
      .ks-item{
        height:36px;
        margin-left:22px;
        margin-bottom:18px;
        background: #FFFFFF;
        border-radius: 4px;
        font-size: 16px;
        font-family: PingFangSC-Medium, PingFang SC;
        font-weight: 500;
        color: #1890FF;
        line-height: 36px;
        position:relative;
        padding:0 20px;
        /deep/ .el-input__inner{
          border:0;
          height:100%;
          width:100px;
          line-height:30px;
          padding:0;
        }
        span{
          display:block;
          text-align:center;
          width:100%;
          height:100%;
          cursor: pointer;

        }
        i{
          position:absolute;
          top:-7px;
          right:-7px;
          line-height:16px;
          text-align:center;
          color:#E02020;
          font-size:14px;
          cursor: pointer;
        }
      }
      .ks-blue{
        border: 1px solid #1890FF;
      }
      .ks-gray{
        border: 1px solid #797979;
      }
    }
  }
}
 </style>



