<template>
  <PopUp :flow='scfjStatus'>
    <div class="box">
      <div class="container">
        <i class="iconfont close" @click='closeScfj'>&#xe648;</i>
        <div class="title clearfix">
          <i class="iconfont title-img">&#xe667;</i>
          <span class="title-span">{{$t('bed.qrsc')}}</span>
        </div>
        <div class="scks-content">
          <p>{{$t('bed.scfjts')}}</p>
        </div>
        <div class="ipt-group clearfix">
          <a href="javascript:void(0)" class="on" @click='deleteRoomFn'>{{$t('button.confirm')}}</a>
          <a href="javascript:void(0)" class="off" @click='closeScfj'>{{$t('button.close')}}</a>
        </div>
      </div>
    </div>
  </PopUp>
</template>

<script>
import popUp from '../../components/popUp'
import {deleteRoom } from '@/api/bed'
import {getDepartmentListD} from '@/api/monitor'
var that= {};
  export default {
    name: 'scfj',
      data(){
          return {

          }
      },
      props:{
        scfjStatus:{
          type:Boolean
        },
        dltRoomObj:{
          type:Object
        }
      },
      beforeCreate(){
          that = this;
      },
      mounted:function(){
        console.log(that.dltRoomObj)
      },
      methods:{
        closeScfj(){
          that.$emit('getscfjStatus',!that.scfjStatus);
        },
        deleteRoomFn(){
          var data={
            roomId:that.dltRoomObj.roomId
          }
          deleteRoom(data).then((res)=>{
            if(res.success && res.code==200){
              that.success(that.$t("bed.sccg"))
              that.$emit('getscfjStatus',!that.scfjStatus,1);
            }
          })
        },
      },
      components:{
          'PopUp': popUp,
      }
  }
</script>
<style scoped lang="scss">
.scks-content{
  border:0;
  width:805px;
  display:flex;
  justify-content: center;
  margin:0 42px;
  flex-direction: row;
  p{
    font-size: 22px;
    font-family: PingFangSC-Medium, PingFang SC;
    font-weight: 500;
    color: #232323;
    line-height: 30px;
    letter-spacing: 1px;
  }
}
</style>



