<template>
  <PopUp :flow='sccwStatus'>
    <div class="box">
      <div class="container">
        <i class="iconfont close" @click='closeSccw'>&#xe648;</i>
        <div class="title clearfix">
          <i class="iconfont title-img">&#xe62f;</i>
          <span class="title-span">{{$t('bed.qrsc')}}</span>
        </div>
        <div class="scks-content">
          <p>{{$t('bed.sccwts')}}</p>
        </div>
        <div class="ipt-group clearfix">
          <a href="javascript:void(0)" class="on" @click='deleteBedFn'>{{$t('button.confirm')}}</a>
          <a href="javascript:void(0)" class="off" @click='closeSccw'>{{$t('button.close')}}</a>
        </div>
      </div>
    </div>
  </PopUp>
</template>

<script>
import popUp from '../../components/popUp'
import {deleteDepart } from '@/api/bed'
import {getDepartmentListD} from '@/api/monitor'
var that= {};
  export default {
    name: 'sccw',
      data(){
          return {

          }
      },
      props:{
        sccwStatus:{
          type:Boolean
        },
        deleteIndex:{
          type:Number
        },
        roomObj:{
          type:Object
        }
      },
      beforeCreate(){
          that = this;
      },
      mounted:function(){

      },
      methods:{
        closeSccw(){
          that.$emit('getsccwStatus',!that.sccwStatus);
        },
        deleteBedFn(){
          that.roomObj.beds.splice(that.deleteIndex,1);
          that.$emit('getsccwStatus',!that.sccwStatus,1);
        },
      },
      components:{
          'PopUp': popUp,
      }
  }
</script>
<style scoped lang="scss">
.scks-content{
  border:0;
  width:805px;
  display:flex;
  justify-content: center;
  margin:0 42px;
  flex-direction: row;
  p{
    font-size: 22px;
    font-family: PingFangSC-Medium, PingFang SC;
    font-weight: 500;
    color: #232323;
    line-height: 30px;
    letter-spacing: 1px;
  }
}
</style>



