import service from '../utils/http'
/*查询房间*/
export function getRoomsList(data) {
  return service({
    url: '/hospital/department',
    method: 'get',
    params:data
  })
}
export function updateHospital(data) {
  return service({
    url: '/hospital/updateHospital',
    method: 'post',
    data:data
  })
}
export function addDepartment(data) {
  return service({
    url: '/hospital/department',
    method: 'post',
    data:data
  })
}
export function editDepartment(data) {
  return service({
    url: '/hospital/department',
    method: 'put',
    data:data
  })
}
export function deleteDepart(data) {
  return service({
    url: '/hospital/department',
    method: 'delete',
    data:data
  })
}
/*新增房间*/
export function addRoom(data) {
  return service({
    url: '/hospital/room',
    method: 'post',
    data:data
  })
}
/*编辑房间*/
export function editRoom(data) {
  return service({
    url: '/hospital/room',
    method: 'put',
    data:data
  })
}
/*删除房间*/
export function deleteRoom(data) {
  return service({
    url: '/hospital/room',
    method: 'delete',
    data:data
  })
}



